<script>
export default {
  name: "Header",
  props: {
    fixed: Boolean,
  },
  methods: {
    handleScroll() {
      let header = document.querySelector("#header");
      if (window.scrollY > 60 && !header.className.includes("is-fixed")) {
        header.classList.add("is-fixed");
      } else if (window.scrollY < 60) {
        header.classList.remove("is-fixed");
      }

      /*if (document.documentElement.clientWidth > 1199) {
        let panel = document.querySelector("#search-panel");
        if (window.scrollY > document.documentElement.clientHeight && !panel.className.includes('is-sticky')) {
          panel.classList.add('is-sticky');
        } else if (window.scrollY < document.documentElement.clientHeight) {
          panel.classList.remove('is-sticky');
        }
      }*/
    },
  },
  created() {
    window.addEventListener("scroll", this.handleScroll);
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
};
</script>

<template>
  <header
    id="header"
    :class="{
      'is-always-fixed': fixed,
    }"
    class="header"
  >
    <div class="container">
